@import '../../style/vars'; 

.character-sidebar {
    flex-basis: 40%;
    padding: 25px 50px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    box-shadow: (2px 2px 4px 4px #00000040);
    
    & .character-block {
        display: flex;
        gap: 25px;

        &__name {
            display: flex;
            flex-direction: column;
            justify-content: space-between;                
        }
        & .char-btn {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &__img {

            img {
                object-fit: cover;
                width: 150px;
                height: 150px;
            }
        }
    }
    
    & .char-desc{
        font-size: 0.5rem;
        font-weight: normal;           
    }
    
    & .char-comics{
        &__title {
            font-size: 0.64rem;
        }

        &__list {
            &_item {
                box-shadow:0px 4px 4px #00000040;
                padding: 5px 0 5px 10px;
                line-height: 16.4px;
                font-size: 0.5rem;
                font-weight: normal;
                margin-bottom: 10px;
                transition: all 0.3s ease;
                &:first-child {
                    margin-top: 10px;
                }

                &:last-child {
                    margin: 0;
                }
                &:hover {
                    cursor: pointer;
                    transform: scale(1.04);
                    box-shadow: 2px 2px 4px 2px #741e28;
                }
            }

        }
    }
    @media (max-width: 800px) {
        padding: 10px 5px 10px 15px;
        
        order: -1;
    }
}

@keyframes pulse {
    from {
        opacity: 0.2;
    }
    to {
        opacity: 1;
    }
}

@keyframes hop {
    0% {
        background-color: #5C5C5C;
    }
    15% {
        background-color: #524a4a;
    }
    25% {
        background-color: #322222;
    }
    35% {
        background-color: #442121;
    }
    40% {
        background-color: #431414;
    }
    50% {
        background-color: #9F0013;
    }
    85% {
        background-color: #524a4a;
    }
}

.empty {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 15px 15px;
    font-size: 18px;
    font-weight: 700;

    span {
        display: block;
    }
    & .select__text {
        text-align: center;
    }
    & .first__line {
        position: relative;
        width: 100%;
        margin-bottom: 30px;
        animation: pulse 3s infinite alternate-reverse;

        &::before {
            content: '';
            top: 50%;
            left: 3%;
            position: absolute;
            border-radius: 50%;
            background-color: #c4c4c4;
            width: 40px;
            height: 40px;
        }

        &::after {
            content: '';
            position: absolute;
            top: 12px;
            right: -12px;
            background-color: #c4c4c4;
            height: 16px;
            width: 85%;
        }
        
    }

    & .bold__line {
        position: relative;
        width: 100%;
        margin-bottom: 35px;
        animation: pulse 3s infinite alternate-reverse;
        &:last-of-type {
            margin-bottom: 50px;
        }

        &::after {
            content: '';
            position: absolute;
            top: 12px;
            right: -12px;
            background-color: #c4c4c4;
            height: 35px;
            width: 100%;
        }        
    }
}

.down__btn {
    width: 100%;
    margin-top: 30px;
    background: #5C5C5C;
    color: #fff;
    text-transform: uppercase;
    animation: hop 10s infinite;

    &:hover {
        transform: scale(1.03);
    }
}

