.characters-list {
    flex-basis: 60%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: 
    ". a ."
    ". a .";
    gap: 15px 30px;

    button {
        margin: 45px 0;
        width: 170px;
        height: 40px;
        grid-column: 2; 
    }
    @media (max-width: 800px) {
        grid-template-columns: repeat(2, 1fr);
        margin: 0 auto;

        button {
            width: 80%;
            margin: 25px auto;
            grid-column: span 2;
        }
    }

    @media (max-width: 500px) {
        display: flex;
        flex-direction: column;
        gap: 30px;
        
        button {
            width: 80%;
            margin: 15px auto;
        }
    }
}

.more {
    display: flex;
    align-items: center;
    justify-content: center;
    grid: 1;
    
    &:disabled {
        filter: grayscale(.7)
    }
}

@import '../../style/vars';

.characters-list__item {
    width: 200px;
    height: 320px;
    background-color: $bg-color;
    box-shadow: (2px 2px 4px 4px #00000040);
       transform: translateY(0);
    transition: all 0.4s ease;

    // &:active {
    //     box-shadow: (0px 5px 20px $main-color);
    //     transform: translateY(-10px);    
    // }
    &:focus {
        transform: translateY(-8px);
    }

    // &:not(:focus) {
    //     box-shadow: (2px 2px 4px 4px #00000040);
    //     transform: translateY(0);
    // }

    img {
        width: 200px;
        height: 200px;
        object-fit: cover;
    }

    & .char-name {
        padding: 15px 0 0 10px;
    }

    &_active {
        box-shadow: (0px 5px 20px $main-color);
        transform: translateY(-8px);
    }

    &:hover, &:active  {
        cursor: pointer;
        box-shadow: (0px 5px 20px $main-color);
        transform: translateY(-8px);
    }
}

@media (max-width: 500px) {
    .characters-list__item {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.spinner {
    display: 'flex';
    justify-content: 'center';
    align-items:'center';
}