@import '../../style/_vars';

.character {
    display: flex;
    justify-content: space-between;
    box-shadow: (2px 2px 4px 4px #00000040);

    &-info {
        display: flex;
        gap: 30px;
        padding: 35px 35px 35px 40px;
        flex-basis: 50%;

        &__img {
            width: 180px;
            height: 180px;
            
            & img {
                width: 180px;
                height: 180px;
                object-fit: cover;    
            } 
        }

        &__text {
            display: flex;
            flex-direction: column;
            gap: 13px;
            justify-content: space-between;

            & .char-desc {
                font-size: 0.5rem;
                font-weight: 400;
                line-height: 16.4px;
            }

            & .char-btn {
                display: flex;
                gap: 30px;
            }

            @media (max-width: 500px) {
                align-items: center;
            }
        }

    }

    &-random {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        padding: 35px 35px 35px 40px;
        flex-basis: 50%;
        background-color: $bg-color;
        background-image: url('../../resources/img/bg_random.svg');
        background-repeat: no-repeat;
        background-position: bottom 25% right -10%;

        & .random-text {
            // text-align: justify;
            max-width: 70%;
            line-height: 28px;
            font-size: 0.85rem;
            color: #fff;
        }
    }

    @media (max-width: 800px) {
        flex-direction: column;
        gap: initial;

        & .character-random {
            order: -1;
            gap: 20px;
            align-items: center;
        }
    }

    @media (max-width: 500px) {
        & .character-info {
            flex-direction: column;

            &__img {
                margin: 0 auto;
            }
        }
    }
}